@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:wght@700;900&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Barlow", sans-serif;
  font-weight: 600;
  color: white;
}

header {
  background-image: url(/images/mobile/image-header.jpg);
  height: 600px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  text-align: center;
  position: relative;
}
header .header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
}
header .header .burger {
  cursor: pointer;
}
header .header .burger span {
  display: block;
  width: 25px;
  height: 2px;
  margin: 6px 0;
  background-color: white;
}
header .header .logo-container {
  height: 28px;
}
header .header .logo-container img {
  height: 100%;
}
header h1 {
  font-family: "Fraunces", serif;
  font-weight: 700;
  font-size: 2.5rem;
  text-transform: uppercase;
  margin-top: 60px;
}
header .arrow {
  margin-top: 70px;
}

.transform-image {
  height: 350px;
  background-image: url(/images/mobile/image-transform.jpg);
  background-position: center center;
  background-size: cover;
  background-size: 100% 100%;
}

.stand-out {
  height: 350px;
  background-image: url(/images/mobile/image-stand-out.jpg);
  background-position: center center;
  background-size: cover;
  background-size: 100% 100%;
}

.graphic-design {
  height: 500px;
  background-image: url(/images/mobile/image-graphic-design.jpg);
  background-position: center center;
  background-size: cover;
  background-size: 100% 100%;
}

.photography {
  height: 500px;
  background-image: url(/images/mobile/image-photography.jpg);
  background-position: center center;
  background-size: cover;
  background-size: 100% 100%;
}

.transform {
  text-align: center;
  padding: 20px;
}
.transform h2 {
  font-size: 1.5rem;
  font-family: "Fraunces", serif;
  font-weight: 900;
  color: #23303e;
}
.transform p {
  font-size: 1rem;
  line-height: 1.6;
  color: #818498;
  margin: 25px 0 42px;
}

.graphic {
  text-align: center;
  padding: 20px;
  position: absolute;
  transform: translateY(-200px);
  color: #25564b;
}
.graphic h2 {
  font-size: 1.5rem;
  font-family: "Fraunces", serif;
  font-weight: 900;
}
.graphic p {
  font-size: 1rem;
  line-height: 1.6;
  margin: 25px 0 42px;
}

.blue {
  color: #19536b !important;
}

section {
  text-align: center;
  margin-bottom: 40px;
}
section a {
  text-decoration: none;
  color: #23303e;
  text-transform: uppercase;
  font-family: "Fraunces", serif;
  font-size: 1rem;
  letter-spacing: 1px;
  font-weight: 900;
}
section .yellow {
  border-bottom: 5px solid #fad400;
}
section .orange {
  border-bottom: 5px solid #fe7867;
}

.d-none {
  display: none !important;
}

.d-block {
  display: block !important;
}

.testimonials {
  color: #5a636c;
  text-align: center;
  padding: 50px 0;
}
.testimonials h3 {
  font-family: "Fraunces", serif;
  color: #a7abae;
  text-transform: uppercase;
  letter-spacing: 0.2em;
  padding-bottom: 50px;
}
.testimonials img {
  height: 50px;
  border-radius: 50%;
}
.testimonials p {
  padding-top: 20px;
  line-height: 2em;
}
.testimonials .author {
  font-family: "Fraunces", serif;
  color: #23303e;
  font-weight: 900;
  display: block;
  padding: 20px 0px 50px;
}
.testimonials .author span {
  padding-top: 10px;
  font-size: 0.8rem;
  font-family: "Barlow", sans-serif;
  color: #a7abae;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 0;
}
.gallery .milk {
  height: 200px;
  width: 50%;
  background-image: url(/images/mobile/image-gallery-milkbottles.jpg);
  background-position: center center;
  background-size: cover;
  background-size: 100% 100%;
}
.gallery .orange {
  height: 200px;
  width: 50%;
  background-image: url(/images/mobile/image-gallery-orange.jpg);
  background-position: center center;
  background-size: cover;
  background-size: 100% 100%;
}
.gallery .cone {
  height: 200px;
  width: 50%;
  background-image: url(/images/mobile/image-gallery-cone.jpg);
  background-position: center center;
  background-size: cover;
  background-size: 100% 100%;
}
.gallery .sugar {
  height: 200px;
  width: 50%;
  background-image: url(/images/mobile/image-gallery-sugar-cubes.jpg);
  background-position: center center;
  background-size: cover;
  background-size: 100% 100%;
}

footer {
  background-color: lightcyan;
  text-align: center;
}
footer .logo-footer {
  padding: 50px 20px;
}
footer ul {
  display: flex;
  justify-content: space-around;
  padding-bottom: 80px;
}
footer ul li {
  list-style: none;
}
footer ul li a {
  text-decoration: none;
  color: #458c7e;
}
footer .social-media {
  padding-bottom: 70px;
}
footer .social-media img {
  padding: 0 15px;
}

.menu {
  position: absolute;
  background-color: white;
  width: 250px;
  height: 250px;
  top: 80px;
  right: 20px;
}
.menu ul {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 100%;
  padding: 10px 0;
}
.menu ul .contact {
  text-transform: uppercase;
}
.menu ul .contact a {
  font-family: "Fraunces", serif;
  font-weight: 700;
  padding: 15px 25px;
  color: #23303e;
  background-color: #fad400;
  border-radius: 30px;
}
.menu ul li {
  list-style: none;
  cursor: pointer;
}
.menu ul li a {
  text-decoration: none;
  color: #a7abae;
}

.menu::before {
  content: "";
  border-bottom: 20px solid white;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  position: absolute;
  top: -10px;
  right: -10px;
  transform: rotate(-90deg);
}

@media (min-width: 600px) {
  header {
    background-image: url(/images/desktop/image-header.jpg);
  }
  header .burger {
    display: none;
  }
  header .desktop-menu {
    display: block !important;
  }
  header .desktop-menu ul {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  header .desktop-menu ul .desktop-contact {
    text-transform: uppercase;
  }
  header .desktop-menu ul .desktop-contact a {
    font-family: "Fraunces", serif;
    font-weight: 700;
    padding: 15px 25px;
    color: #23303e;
    background-color: white;
    border-radius: 30px;
  }
  header .desktop-menu ul li {
    list-style: none;
    cursor: pointer;
    padding: 0px 20px;
  }
  header .desktop-menu ul li a {
    text-decoration: none;
    color: white;
  }

  section {
    margin-bottom: 0px !important;
  }

  .stand-out {
    background-image: url(/images/desktop/image-stand-out.jpg);
  }

  .graphic-design {
    background-image: url(/images/desktop/image-graphic-design.jpg);
  }

  .photography {
    background-image: url(/images/desktop/image-photography.jpg);
  }

  .transform-container {
    display: flex;
    flex-direction: row-reverse;
    align-content: center;
  }
  .transform-container .transform {
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 12.5%;
    text-align: left;
  }
  .transform-container .transform h2,
.transform-container .transform p,
.transform-container .transform a {
    align-self: flex-start;
  }
  .transform-container .transform h2 {
    font-size: 2.5rem;
  }
  .transform-container .transform-image {
    display: block;
    flex-basis: 50%;
    height: 600px;
    background-image: url(/images/desktop/image-transform.jpg);
  }

  .stand-out-container {
    display: flex;
    align-content: center;
  }
  .stand-out-container .transform {
    flex-basis: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 12.5%;
    text-align: left;
  }
  .stand-out-container .transform h2,
.stand-out-container .transform p,
.stand-out-container .transform a {
    align-self: flex-start;
  }
  .stand-out-container .transform h2 {
    font-size: 2.5rem;
  }
  .stand-out-container .stand-out {
    display: block;
    flex-basis: 50%;
    height: 600px;
    background-image: url(/images/desktop/image-stand-out.jpg);
  }

  .design-container {
    display: flex;
    align-content: center;
  }
  .design-container .first {
    flex-basis: 50%;
    position: relative;
  }
  .design-container .first .graphic-design {
    display: block;
    background-image: url(/images/desktop/image-graphic-design.jpg);
    height: 600px;
  }
  .design-container .second {
    flex-basis: 50%;
    position: relative;
  }
  .design-container .second .photography {
    display: block;
    background-image: url(/images/desktop/image-photography.jpg);
    height: 600px;
  }
  .design-container .graphic {
    position: absolute;
    padding: 0 25%;
  }

  .testimonials {
    padding-top: 70px;
  }
  .testimonials .testimonial-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 20px 50px;
  }
  .testimonials article {
    padding: 0 50px;
  }
  .testimonials .author {
    padding-top: 50px;
  }

  .gallery {
    flex-wrap: nowrap;
  }
  .gallery .milk {
    width: 25%;
    background-image: url(/images/desktop/image-gallery-milkbottles.jpg);
    height: 400px;
  }
  .gallery .orange {
    width: 25%;
    background-image: url(/images/desktop/image-gallery-orange.jpg);
    height: 400px;
  }
  .gallery .cone {
    width: 25%;
    background-image: url(/images/desktop/image-gallery-cone.jpg);
    height: 400px;
  }
  .gallery .sugar {
    width: 25%;
    background-image: url(/images/desktop/image-gallery-sugarcubes.jpg);
    height: 400px;
  }

  footer ul {
    justify-content: center;
  }
  footer ul li {
    padding: 0 25px;
  }
}